import { createStore } from 'vuex'
const store = createStore({

 // 데이터를 저장 하는 장소
  state(){
    return {
        likes: [], 
        likesClick: false       
    }
  },

  // 데이터 수정방법을 작성하는 장소
  mutations: {  
      추가(state, payload){
        state.likes = payload;
      },

      좋아요(state){
          if( state.likesClick != true){
            state.likes ++;
            state.likesClick = true;
          }else{
            state.likes --;
            state.likesClick = false;
          }
      }

  }

})

export default store